@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@700&family=Roboto+Slab&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}
body {
  font-family: serif, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d7cebd;
  padding: 12px;
  margin:0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.divider,.single_divider {
	padding: 1px;
	border: 2px solid #654321;
	margin: 10px 0;
	border-left: 0;
	border-right: 0;
}
.single_divider {
	border-top:0;
}

.footer {
	font-size: 14px;
	font-weight: bold;
	text-align: center;
}
.footer span {
	font-size: 16px;
}
.footer .developer {
	padding: 10px 0;
}
.footer .divider {
	width:90%;
	margin: 10px auto;
}
.categories {
	display: flex;
	flex-direction: row;
	overflow: scroll;
}
.categories button {
	margin: 0 5px;
}
.categories button:focus {
	background-color: white;
	color: #654321;
}

.header h1 {
	margin: 12px auto;
	padding: 8px 16px;
	border-top: 2px solid black;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	text-align: center;
	border-bottom: 2px solid black;
	color: #654321;
	position: relative;
	font-family: serif;
}
.header h1::before {
	content: "the";
	position: absolute;
	font-size: 16px;
	background-color:#d7cebd;
	left: 50%;
	top: -14px;
	text-transform: uppercase;
	transform: translateX(-50%);
	padding: 3px 5px;
}
 .header .timeDet {
	 display: flex;
	 flex-direction: row;
	 justify-content: space-between;
	 align-items: center;
	 padding:0 16px;
	 font-family: sans-serif;
	 font-weight: bold;
	 color: #654321;
 }

.title {
	font-family: 'Roboto';
	font-weight: 1000;
	font-size: 1.8em;
	text-transform: uppercase;
	width: 100%;
	padding-bottom: 5px;
	text-align: left;
}
img {
	-webkit-filter: grayscale(1);
	        filter: grayscale(1);
	width: 90%;
}
p {
	text-align: left;
	font-weight: 600;
	text-transform: uppercase;
	font-family: 'Roboto Slab';
}
span {
	font-size: 30px;
	line-height: 1;
	font-weight: 1000;
	color: #2b1d14;
}
.topnews .featuredTitle {
	font-size: 2.5em;
	background-color: #654321;
	color: white;
	line-height:1;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	text-transform: uppercase;
	font-weight: bold;
}
button {
	font-size: 1em;
	color: white;
	text-transform: uppercase;
	font-weight: bold;
	padding: 8px;
	margin: 8px 0;
	border:0;
	background-color: #654321;
}
button a {
	color: white;
	text-decoration: none;
}
.imgCon {
	display: flex;
	flex-direction: row;
}
.imgCon .publisher {
	-ms-writing-mode: tb-rl;
	    writing-mode: vertical-rl;
	color: white;
	background-color: #654321;
	padding: 0 4px;
}
.imgCon .publisher {
	text-transform: uppercase;
	font-weight: 900;
}

.smallnews {
	margin: 8px;
	margin-left:0;
	position: relative;
	overflow: hidden;
}
.smallnews .title {
	font-size: 1em;
	text-align: left;
}
.smallnews p {
	font-size: 0.7em;
	width: 100%;
	text-align: left;
}
.smallnews .imgCon {
	max-width: 100%;
}
.smallnews .publisher {
	font-size: 0.8em;
}

.loader {
    height:100vh;
    display:flex;
    justify-content:center;
    align-items:center;
    flex-direction:row;
}
.loader div {
    height: 0px;
    width: 5px;
    background-color:#654321;
    margin:0 5px;
    -webkit-animation:rise 1s linear infinite;
            animation:rise 1s linear infinite;
}
@-webkit-keyframes rise {
    0% {
        height:0px;
    }
    50% {
        height:40px;
    }
    100% {
        height: 0px;
    }
}
@keyframes rise {
    0% {
        height:0px;
    }
    50% {
        height:40px;
    }
    100% {
        height: 0px;
    }
}
.loader div:nth-child(1) {
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}
.loader div:nth-child(2) {
    -webkit-animation-delay: 0.5s;
            animation-delay: 0.5s;
}
.loader div:nth-child(3) {
    -webkit-animation-delay: 0.7s;
            animation-delay: 0.7s;
}
.loader div:nth-child(4) {
    -webkit-animation-delay: 0.9s;
            animation-delay: 0.9s;
}

